.Settings {
  width: 100%;
  background-color: #fff;
  font-size: 1.1em;
  text-align: center;
  z-index: 2;
  border-radius: 0.25rem;
  padding: 1vh 0;
  -webkit-transform: all 0.2s ease-in;
      -ms-transform: all 0.2s ease-in;
          transform: all 0.2s ease-in;
}

.Settings__Section {
  display: inline-block;
  margin: 0 1rem;
}

.Settings__Row {
  display: inline-block;
}

.Settings span {
  font-size: 1.1em;
  cursor: pointer;
}

.Settings--Center {
  -webkit-transform: all 0.2s ease-in;
      -ms-transform: all 0.2s ease-in;
          transform: all 0.2s ease-in;
  -webkit-box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
          box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  padding: 2vh 0;
  position: absolute;
  top: 40%;
}

@media (max-width: 767px) {
  .Settings__Row {
    display: block;
  }
}
@media (max-width: 500px) {
  .Settings button {
    font-size: 0.7em;
    padding: 0.4rem;
  }
}



